.trending-articles{
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 1em;
}

.home-container{
    display: flex;
    justify-content: center;
    gap: 1em;
}

.sidebar{
    display: flex;
    flex-direction: column;
    width: 20em;
    margin-top: 2em;
}



@media (max-width: 820px) {
 
    .trending-articles{
        display: flex;
        flex-direction: column-reverse;
        width: 100%;
        padding: 0;
    }
    

}